@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    font-family: 'Roboto', sans-serif;
}
html{
    scroll-behavior: smooth;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
::-webkit-scrollbar {
    width: 0.6vw;
}
::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0);
}
::-webkit-scrollbar-thumb {
    background-color: hsl(140, 77%, 38%);
    border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
    background-color: hsl(140, 77%, 38%);
}   
a{
    color: inherit;
    text-decoration: none!important;
}
a:hover{
    text-decoration: none!important;
}
body{
    background-color: rgb(245, 255, 245)!important;
}
.nav{
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 3vw !important;
    height: 65px;
    width: 100%;
    background-color: hsla(140, 77%, 38%, 0.9);
    z-index: 5;
    transition: all 0.5s ease-in-out;
}
.nav .logo{    
    background-image: url(/GSASports/static/media/logo.d4ed9c43.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    height: 55px; 
    width: 150px;
    transition: all 0.5s ease-in-out;
}
.navitems{
    height: 100%;
    width: 35%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: black;
    font-weight: 600;
    max-width: 1000px;
}
.navitems a{
    height: 100%;
    width: 100%;
}
.navitems .navele{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    position: relative;
}
.navitems .navele::after{
    content: '';
    position: absolute;
    bottom: 5px;
    left: 25%;
    right: 25%;
    height: 2px;
    background-color: white;
    opacity: 0;
    transform: translateY(10px) scale(0);
    transition: 0.5s ease-in-out;
}
.navitems .navele:hover::after{
    opacity: 1;
    transform: translateY(0px) scale(1);
}
.nav .nav_sec{
    display: flex;
    margin-right: 3vw;
}
.nav .nav_sec .opt{
    height: 25px;
    width: 25px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}
.nav .nav_sec .opt1{
    margin-right: 30px;
    background-image: url(/GSASports/static/media/shopping.f0d47d21.svg);
}
.nav .nav_sec .opt2{
    background-image: url(/GSASports/static/media/unknown.620224f4.svg);
    position: relative;
}
.nav .nav_sec .opt2 .dropdown{
    background-color: white;
    display: flex;
    flex-direction: column;
    opacity: 1;
    position: absolute;
    top: 120%;
    text-align: center;
    right: 0%;
    color: black;
    transition: 1s ease-in-out;
    overflow: hidden;
    opacity: 0;
    height: 0px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    cursor: pointer;
    transform: translateY(30px);
    transform: 0.2s ease-in-out;
}
.nav .nav_sec .opt2:hover .dropdown{
    opacity: 1;
    height: auto;
    height: initial;
    transform: translateY(-5px);
}
.nav .nav_sec .opt2 .dropdown .option{
    padding: 15px 20px;
    width: 180px;
    font-size: 14px;
    transition: 0.5s ease-in-out;
}
.nav .nav_sec .opt2 .dropdown .option:hover{
    color: white;
    background-color: hsl(140, 77%, 38%);
    cursor: pointer;
}
.nav .nav_sec .opt2 a{
    color: black;
}
.menu-btn{
    position: relative;
    display: none;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 60px;
    cursor: pointer;
    overflow: hidden;
    transition: all .5s ease-in-out;
}
.menu-btn_burger {
    width: 30px;
    height: 4px;
    background: white;
    border-radius: 5px;
    transition: all .5 ease-in-out;
    cursor: pointer;
}
.menu-btn_burger::before,
.menu-btn_burger::after {
    content: '';
    position: absolute;
    width: 30px;
    height: 4px;
    background: white;
    border-radius: 5px;
    transition: all .5s ease-in-out;
}
.menu-btn_burger::before {
    transform: translateY(-10px);
}
.menu-btn_burger::after {
    transform: translateY(10px);
}
.menu-btn.open .menu-btn_burger {
    transform: translateX(-50px);
    background: transparent;
    box-shadow: none;
}
.menu-btn.open .menu-btn_burger::before{
    transform: rotate(45deg) translate(35px, -35px);
}
.menu-btn.open .menu-btn_burger::after{
    transform: rotate(-45deg) translate(35px, 35px);
}

.backdrop{
    position: fixed;
    height: 100vh;
    width: 100vw;
    background-color: rgba(142, 255, 142, 0.5);
    opacity: 0;
    z-index: -1;
    transition: opacity 0.75s ease-in-out;
}
.backdrop.back{
    opacity: 1;
    z-index: 3;
}
.sidebar{
    position: fixed;    
    width: 100%;
    opacity: 0;
    background-color: rgba(0, 182, 61, 0.6);
    z-index: 4;
    color: white;
    transition: 0.75s ease-in-out;
    transform: translateY(-150%);
    overflow-y: scroll;
    overflow-x: hidden;
}
.sidebar.slide{
    transform: translateY(65px);
    opacity: 1;
}
.sidebar .list{
    display: flex;
    flex-direction: column;
}
.sidebar .list .listele{
    padding: 20px;
    font-size: 18px;
    font-weight: 500;
    border-bottom: 2px solid white;
    text-align: center;
}
.sidebar .list .listele{
    width: 100%;
    height: 100%;
    color: white;
}
.sidebar .list .listele.no{
    border: none;
}
.gototop a{
    position: fixed;
    bottom: 20px;
    right: 3vw;
    height: 40px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    color: white;
    background-color: rgb(0, 175, 175);
    border-radius: 50px;
    transform: translateY(20px);
    opacity: 0;
    transition: 0.5s ease-in-out;
    z-index: 4;
}
.gototop.active a{
    opacity: 1;
    transform: translateY(0px);
} 
.gototop:hover a{
    background-color: rgb(0, 255, 255);
}

@media only screen and (max-width: 1184px) {
    .menu-btn, .menu-btn_burger{
        display: flex;
    }
    .nav .logo{   
        margin-left: 5vw;
    }
    .navitems{
        display: none;
    }
}

@media only screen and (max-width: 709px) {
    .contact_tab{
        display: none;
    }
    .nav{
        top: 0px;
    }
    .nav .logo{    
        transform: scale(0.8);
    }
    .phone_mob{
        display: flex;
    }
    .mail_mob{
        display: flex;
    }
}

@media only screen and (max-width: 500px) {
    
    .nav .logo{    
        margin-left: 2vw;
        height: 60px; 
        width: 120px;
    }
    .sidebar .list .listele{
        padding: 15px;
        font-size: 14px;
    }
    .sidebar .list .listele:first-child{
        font-size: 16px;
    }
    .menu-btn{
        width: 40px;
        height: 50px;
    }
    .menu-btn_burger,
    .menu-btn_burger::before,
    .menu-btn_burger::after {
        width: 25px;
        height: 3px;
    }
    .menu-btn_burger::before {
        transform: translateY(-8px);
    }
    .menu-btn_burger::after {
        transform: translateY(8px);
    }
}
@media only screen and (max-width: 340px) {
    
    .nav .logo{    
        margin-left: 0px;
        height: 50px; 
        width: 200px;
    }
    .sidebar .list .listele{
        padding: 10px;
    }
}


.phn_modal .modal-header{
    border: none;
    background-color: transparent !important;
}
.phn_modal .modal-content{
    margin-top: 100px;
}
.phn_modal .modal-body{
    position: relative;
    padding: 0;
    padding-top: 10px;
}
.phn_modal .modal-body .phn{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    font-size: 16px;
    overflow: hidden;
}
.phn_modal .modal-body .phn .line{
    height: 2px;
    width: 100%;
    background-color: #087dc0;
    margin-top: 10px;
    transform: translateX(-120%);
    transition: 1s ease-in-out;
}
.phn_modal .modal-body .phn:hover .line{
    transform: translateX(0%);
}
.phn_modal .modal-body .phn span{
    font-size: 18px;
    font-weight: 500;
    margin-right: 10px;
}
.phn_modal .modal-body .close_{
    position: absolute;
    right: 0;
    top: 0;
    outline: none;
    transform: translateY(-30px);
    font-size: 20px;
    color: white;
    cursor: pointer;
}

.phn_modal.whtsapp .modal-body .phn i{
    margin-right: 20px;
    background-color: #25D366;
    border-radius: 100%;
    padding: 8px 9px;
    font-size: 18px;
    color: white;
}
.phn_modal.whtsapp .modal-body .phn .line{
    background-color: #25D366;
}
.footer{
