    background-color: hsl(120, 61%, 34%);
    padding: 50px 5vw;
    padding-bottom: 30px;
    color: rgb(231, 231, 231);
    transition: 1s ease-in-out;
}
.footer a:hover{
    color: white;
    cursor: pointer;
}
.footer:hover{
    background-color: hsl(120, 61%, 37%);
}
.footer .section1{
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer .section1 .img{
    background-image: url(/GSASports/static/media/logo.d4ed9c43.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    height: 200px;
    width: 100%;
}
.footer .section2{
