}
.footer .row2{
    margin-top: 30px;
    padding-top: 30px;
    border-top: 4px solid rgb(255, 255, 255);
}
.footer .row2 .company_line{
    font-size: 16px;
    font-weight: 500;
    text-align: center;
}
@media only screen and (max-width:991px){

    .phn_modal .modal-body .phn .line{
