    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 400px;
}
.login_cont .login .email,
.login_cont .login .password{
    width: 100%;
    margin-bottom: 20px;
}
.login_cont .login:before{
    z-index: -1;
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(/GSASports/static/media/loginpic.338150ca.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    box-shadow: rgba(0, 0, 0, 0.69) 0px 5px 15px;
}

.login_cont .login .form{
    width: 100%;
    position: relative;
    height: 70px;
    overflow: hidden;
    color: black;
}
.login_cont .login .form input{
    width: 100%;
    height: 100%;
    font-size: 16px;
    color: #595f6e;
    padding-top: 30px;
    padding-left: 10px;
    border: none;
    outline: none;
    background: rgba(255, 255, 255, 0);
}
.login_cont .login .form label{
    font-size: 18px;
    position: absolute;
    bottom: 0px;
    left: 0%;
    width: 100%;
    height: 100%;
    pointer-events: none;
    border-bottom: 1px solid black;
}
.login_cont .login .form label::after {
    content: "";
    position: absolute;
    left: 0px;
    bottom: -1px;
    width: 100%;
    height: 100%;
    border-bottom: 4px solid hsl(209, 100%, 50%);
    transform: translateX(-100%);
    transition: transform 0.3s ease;
}

.login_cont .login .content-name{
    position: absolute;
    bottom: 0px;
    left: 0px;
    transition: all 0.3s ease;
    font-weight: 500;
}

.login_cont .login .form input:focus + .label-name .content-name,
.login_cont .login .form input:valid + .label-name .content-name {
    transform: translateY(-120%);
    font-size: 19px;
    color: hsl(209, 100%, 38%);

}

