    transition: 0.5s ease-in-out;
}
.signup_cont .signup .notes textarea:hover{
    border: 1px solid hsl(140, 77%, 38%);
}
.signup_cont .signup .submit{
    grid-column: 1 / span 2;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    justify-self: center;
    margin-top: 20px;
    padding: 10px 25px;
    background-color: hsl(140, 77%, 38%);
    color: white;
    border-radius: 50px; 
    font-size: 18px;
    transition: 0.5s ease-in-out;
    border: 0px;
    cursor: pointer;
}
.signup_cont .signup .submit:hover{
    text-decoration: none;
    background-color: hsl(140, 87%, 48%);
    transform: translateY(-10px);
    box-shadow: 0 5px 10px rgba(78, 78, 78, 0.4);
}
.signup_cont .signup .msg{
    margin-top: 30px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: 0.5s ease-in-out;
    text-decoration: underline;
    color: black;
}
.signup_cont .signup .msg:hover{
    color: hsl(140, 100%, 50%);
}
@media only screen and (max-width:550px){

    .form{
        font-size: 18px;
